let TLMNEvent = {};
TLMNEvent.RESPONSE_NAME = {
    SIT_ON_RES : "tl1",
    SIT_OUT_RES : "tl2",
    STAND_UP_RES : "tl3",
    PRE_START_RES : "tl4",
    START_RES : "tl5",
    FIRE_RES : "tl6",
    KICK_RES : "tl7",
    FINISH_GAME_RES : "tl8",
    LOAD_TABLE_RES : "tl9",
    FOLD_RES : "tl10",
    USER_TURN_RES : "tl11",
    NEW_ROUND: "tl12"
};
TLMNEvent.UserSitOnEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(TLMNEvent.RESPONSE_NAME.SIT_ON_RES);
    },
    fromEvent(event){
        let param = event.get(TLMNEvent.RESPONSE_NAME.SIT_ON_RES);
        this.m_deskId = param.getInt("did");
        this.m_userName = param.getUtfString("u");
        this.m_currentUser = param.getUtfString("cu");
        this.m_deskState = param.getUtfString("ds");
        this.m_buyIn = param.getDouble("c");
        return this;
    }
});
TLMNEvent.UserSitOutEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(TLMNEvent.RESPONSE_NAME.SIT_OUT_RES);
    },
    fromEvent(event){
        let param = event.get(TLMNEvent.RESPONSE_NAME.SIT_OUT_RES);
        this.m_username = param.getUtfString("u");
        this.m_isSitOut = param.getBool("io");
        return this;
    }
});
TLMNEvent.UserStandUp = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(TLMNEvent.RESPONSE_NAME.STAND_UP_RES);
    },
    fromEvent(event){
        let param = event.get(TLMNEvent.RESPONSE_NAME.STAND_UP_RES);
        this.m_username = param.getUtfString("u");
        return this;
    }
});
TLMNEvent.PreStartEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(TLMNEvent.RESPONSE_NAME.PRE_START_RES);
    },
    fromEvent(event){
        this.m_isPrestart = event.getBool("ip");
        this.m_prestartTime = event.getInt("t");
        return this;
    }
});
TLMNEvent.StartEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(TLMNEvent.RESPONSE_NAME.START_RES);
    },
    fromEvent(event){
        this.m_currentUser = event.getUtfString("cu");
        this.m_userTime = event.getInt("ut");
        this.m_listCard = event.getByteArray("lc");
        this.m_listActiveUserName = event.getUtfStringArray("lu");
        return this;
    }
});
TLMNEvent.FireEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(TLMNEvent.RESPONSE_NAME.FIRE_RES);
        this.m_type = this.constructor.NONE;
    },
    fromEvent(event){
        this.err = event.containsKey("er") ? event.getUtfString("er") : undefined;
        if(!this.err){
            this.m_username = event.getUtfString("u");
            this.m_cards = event.getByteArray("c");
            this.m_type = event.getByte("t");
        }
        return this;
    }
});
TLMNEvent.FireEvent.ERROR = 0;
TLMNEvent.FireEvent.NONE = 1;
TLMNEvent.FireEvent.CUNG_CAP = 2;
TLMNEvent.FireEvent.TUQUI_CHAT_HEO = 3;
TLMNEvent.FireEvent.TUQUI_CHAT_2HEO = 4;
TLMNEvent.FireEvent.TUQUI_CHAT_3DOI_THONG = 5;
TLMNEvent.FireEvent.THREE_DOI_THONG_CHAT_HEO = 6;
TLMNEvent.FireEvent.FOUR_DOI_THONG_CHAT_HEO = 7;
TLMNEvent.FireEvent.FOUR_DOI_THONG_CHAT_2HEO = 8;
TLMNEvent.FireEvent.FOUR_DOI_THONG_CHAT_3DOI_THONG = 9;
TLMNEvent.FireEvent.FOUR_DOI_THONG_CHAT_TUQUI = 10;
TLMNEvent.KickUserEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(TLMNEvent.RESPONSE_NAME.KICK_RES);
    },
    fromEvent(event){
        let param = event.get(TLMNEvent.RESPONSE_NAME.KICK_RES);
        this.m_resType = param.getInt("rt");
        this.m_username = param.getUtfString("u");
        return this;
    }
});
TLMNEvent.FinishGameEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(TLMNEvent.RESPONSE_NAME.FINISH_GAME_RES);
        this.m_listActiveUserName = [];
        this.m_listChipWin = [];
        this.m_listHandCard = [];
        this.typePerfect = -1;
    },
    fromEvent(event){
        this.m_listActiveUserName = event.getUtfStringArray("lu");
        this.m_listChipWin = event.getDoubleArray("lw");
        let sfsListHandCard = event.getSArray("lc");
        for(let i = 0; i < sfsListHandCard.size();i++){
            let sfsHandCards = sfsListHandCard.get(i).getObject();
            let cards = [];
            for(let j = 0; j < sfsHandCards.size();j++){
                cards.push(sfsHandCards.get(j).getObject());
            }
            this.m_listHandCard[i] = cards;
        }
        this.typePerfect = event.getByte("tp");
        return this;
    }
});
TLMNEvent.LoadTableInfoEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(TLMNEvent.RESPONSE_NAME.LOAD_TABLE_RES);
        this.m_isGameStart = false;
        this.m_isPrestart = false;
        this.m_prestartTime = 0;
        this.m_listDesk = [];
        this.m_listUserSitOut = [];
        this.m_listUser = [];
        this.m_listActiveUserName = [];
        this.m_listHandCard = [];
    },
    fromEvent(event){
        let param = event.get(TLMNEvent.RESPONSE_NAME.LOAD_TABLE_RES);
        this.m_isGameStart = param.getBool("is");
        this.m_isPrestart = param.getBool("ip");
        this.m_prestartTime = param.getInt("pt");

        let sfsListDesk = param.getSArray("ld");
        for(let i = 0; i < sfsListDesk.size(); i++){
            let desk = this.deskFromSObject(sfsListDesk.get(i).getObject());
            this.m_listDesk.push(desk);
        }

        this.m_listUserSitOut = param.getUtfStringArray("luo");
        this.m_listUser = param.getUtfStringArray("lu");
        this.m_listActiveUserName = param.getUtfStringArray("lau");

        let sfsListHandCard = param.getSArray("lc");
        for(let i = 0; i < sfsListHandCard.size();i++){
            let sfsHandCards = sfsListHandCard.get(i).getObject();
            let cards = [];
            for(let j = 0; j < sfsHandCards.size();j++){
                cards.push(sfsHandCards.get(j).getObject());
            }
            this.m_listHandCard[i] = cards;
        }
        return this;
    },
    deskFromSObject(obj){
        let desk = {};
        desk.DeskId = obj.getInt("id");
        desk.UserName = obj.getUtfString("u");
        desk.DeskState = obj.getUtfString("s");
        desk.UserChip = obj.getDouble("c");
        return desk;
    }
});
TLMNEvent.UserTurnEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(TLMNEvent.RESPONSE_NAME.USER_TURN_RES);
    },
    fromEvent(event){
        this.m_username = event.getUtfString("u");
        this.m_time = event.getInt("t");
        return this;
    }
})
TLMNEvent.UserFoldEvent = CasinoEvent._BaseEvent.extend({
    ctor(){
        this._super(TLMNEvent.RESPONSE_NAME.USER_FOLD_RES);
    },
    fromEvent(event){
        this.m_username = event.getUtfString("u");
        return this;
    }
});
window.TLMNEvent = module.exports = TLMNEvent;